.esthethic{
    width: 100vw;
    background-color:#333333;
    display: flex;
    padding-inline: 5vw 15vw;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    position: relative;
    .arrows{
        position: absolute;
        width: 124px ;
        display: flex;
        gap: 1.5rem;
        left: calc(50% - 62px);
        top: 90vh;
        z-index: 4;
        .arrow{
            fill:#DADADA;
            animation: arrow-anim 1s  ease infinite;
            transition: fill 0.5s ease-in-out;
        }
        .inactive-button{
            fill: #949191;
            animation: paused;
        } 
    }
    
}


.esthethic.active{
    .arrows{
        position: fixed;
        left: calc(50% - 62px);
        top: 90vh;
        z-index: 4;
    }
}

.esthethic-section{
    flex: 0 0 auto;
    width: 100vw;
    height: 100%;
    background-color:#333333;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding-top: 5vh;
    padding-bottom: 10vh;
    padding-inline: 5vw 15vw;
    position: relative;
    scroll-snap-align: start;
    overflow-x: hidden;
    scroll-snap-stop: always;
    h2{
        font-family: 'Outfit', sans-serif;
        font-size: 40px; 
        font-style: normal;
        margin: 0;
        color:$red;
        white-space: normal;
        font-size: clamp(1rem, 10vw, 3rem); 
        line-height: 50px;
        letter-spacing: 0.01em;
        
    }
    .text{
        color:$white;
        line-height: 130%;
        padding-inline: 0 5vw;
        font-size: clamp(0.5rem, 4vw, 1rem); ;
        width: 100%;
        max-height: 40vh;
    }
    .logo{
        opacity: 0;
        transition: opacity 2s ease;
        transition: transform 2s ease;
        
        
        svg{
            display: block;
            width: 60vw;
             max-width: 280px;
             height: 45vh;
            object-fit: contain;
            transform: translateX(-40%);
            path{
                stroke-width: 1px;
                stroke:$white;
            }
            
        }
    }
}


.active-slide{
    .logo{
        opacity: 1;
        transition:opacity 1s ease;
    }
}

.esthethic-section .section1{
    flex-direction: column;
    h2{
        font-family: 'Outfit', sans-serif;
        font-weight: $boldFontWeight;
        margin: 0 auto;
        color:$red;
        font-size: 40px; 
        
    }
    .text{
        color:$white;
        line-height: 130%;
        padding-inline: 0 10vw;
        font-size: 1rem;
        font-family: 'Outfit', sans-serif;
        font-weight: $smallFontWeight;
    }
}
.section2{
    .logo{
        svg{
            .forehead{
                stroke: $red;
                stroke-width: 2px;
                animation: head-anim 3s ease-in-out infinite;
            }
        }
    }
}
.section3{
    .logo{
        svg{
            .eyes{
                stroke: $red;
                animation: head-anim 3s ease-in infinite;
            }
        }
    }
}
.section4{
    .logo{
        svg{
            .nouse{
                stroke: $red;
                animation: head-anim 3s ease-in infinite;
            }
        }
    }
}
.section5{
    .logo{
        svg{
            .skull{
                stroke: $red;
                animation: head-anim 3s ease-in infinite;
            }
        }
    }
}
.section6{
    .logo{
        svg{
            .lips{
                stroke: $red;
                animation: head-anim 3s ease-in infinite;
            }
        }
    }
}
.section7{
    .logo{
        svg{
            .podb{
                stroke: $red;
                animation: head-anim 3s ease-in infinite;
            }
        }
    }
}



@keyframes head-anim {
    0%{
        stroke: $red; 
        stroke-width: .7px;
    }
    50%{
        stroke:lighten($color: $red, $amount: 30%) $red; 
        stroke-width: 2px;
    }
    100%{
        stroke: $red; 
        stroke-width: .7px;
    }
}

@keyframes arrow-anim {
    0%{
        fill:#DADADA; 
    }
    50%{
        fill:#fbf9f9;
    }
    100%{
        fill:#DADADA; 
    }
}