.main-head{
    width: 100vw;
    // height: 100%;
  // height: calc(var(--vh, 1vh) * 100);  background-color:$white;
    display: flex;
    flex-direction: row-reverse;
    background-color: $white;
    // gap: 3rem;
    justify-content: center;
    align-items: center;
    padding-inline: 5vw 15vw;
    overflow-x: hidden;
    position: relative;
    
    .hero-nav{
        list-style: none;
        display: flex;
        justify-content: space-between;
        grid-area: 1 / 1 / 2 / 3; 
        margin-inline: 0;
        padding-inline: 0;
        display: none;
    }
    .head-text{
        h1{
           font-family: 'Outfit';
           font-weight: $boldFontWeight;
            margin: 0;
            color:$red;
            word-wrap: break-word;
            font-size: 40px; 
            // line-height: 50px;
            letter-spacing: 0.01em;
        }
        p{
            font-family: 'Outfit', sans-serif;
            font-weight: $smallFontWeight;
            color:$black;
            padding-bottom: 1rem;
            font-size: 1rem; 
        };
    }
    .head-logo{
        align-self: center;
        fill: none;
        
        .hero-logo{
          display: block;
          width: 40vw;
          max-height: 60vh;
          stroke: #66666A;
          object-fit: contain;
        }
    }
  
    
}


.run-line {
  position: absolute;
    // overflow: hidden;
    bottom:5vh;
    animation: marquee 10s ease infinite;
    display: flex;
    gap: 1rem;
    width: 100%;
  }


  
  .run-line span {
    
    display: inline-block;
    white-space: nowrap;
    
    color:$lightgreen
  }
 
  
  
  @keyframes marquee {
    0% {
      left: +200%;
    }
    
    100%{
      left: -100%;
    }
  }