.main-head{
    width: 100vw;
    // height: 100%;
  // height: calc(var(--vh, 1vh) * 100);  background-color:$white;
    display: flex;
    flex-direction: column;
    background-color: $white;
    // gap: 3rem;
    justify-content: space-evenly;
    // padding-inline: 5vw 15vw;
    overflow-x: hidden;
    position: relative;
    
    .hero-nav{
        list-style: none;
        display: flex;
        justify-content: space-between;
        grid-area: 1 / 1 / 2 / 3; 
        margin-inline: 0;
        padding-inline: 0;
        display: none;
    }
    .head-text{
        h1{
           font-family: 'Outfit';
           font-weight: $boldFontWeight;
            margin: 0;
            color:$red;
            word-wrap: break-word;
            font-size: 40px; 
            // line-height: 50px;
            letter-spacing: 0.01em;
            padding-inline: 5vw 15vw;
        }
        p{
            font-family: 'Outfit', sans-serif;
            font-weight: $smallFontWeight;
            color:$black;
            padding-bottom: 1rem;
            font-size: 1rem; 
            padding-inline: 5vw 10vw;
        };
    }
    .head-logo{
        align-self: center;
        fill: none;
        
        .hero-logo{
          display: block;
          width: 50vw;
          max-height: 50vh;
          stroke: #66666A;
          object-fit: contain;
          stroke-dasharray: 4200;
          stroke-dashoffset: 4200;
          animation: anim 20s forwards linear;
          transition: all 5 ease-in;
        }
    }
  
    
}

@keyframes anim{
       
  to{
    opacity: 1;
      stroke-dashoffset: 0;
      stroke: 0.5 rgb(55, 52, 52);
  }
}

.run-line {
  position: absolute;
    overflow: hidden;
    bottom:5vh;
    
    
   
    width: 100%;
  }


  
  .run-line {
    display: flex;
    flex-wrap: nowrap;
    gap: 1rem;
    // animation: marquee 10s ease infinite;
    display: inline-block;
    white-space: nowrap;
    
    color:$lightgreen
  }
 
  
  
  @keyframes marquee {
    0% {
      left: +200%;
    }
    
    100%{
      left: -100%;
    }
  }