.menu-item.menu-five{
    width: 100%;
    height: 100%;
    background-color: $white;
    display: flex;
    flex-direction: column;
    // justify-content: center;
    // padding-inline: 50px;
    gap: 30px;
    footer{
        // align-self:center;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 10vh;
        background-color:$white;
        width:100%;
        text-align: center;
        color: $gray;
        .designed{
            text-decoration: none;
            color: $red;
        }
        p{
            margin: 0;
        }
    }
    .content{
        margin: auto;
        width: 100%;
        height: auto;
        margin-top: 15vh;
        display: grid; 
        gap: 10vh 0px; 
        grid-template-columns: 1fr 1fr; 
        grid-template-areas: 
                            "beauty beauty"
                            "p link"
                            "button button"; 
           .beuty-text{
            grid-area: beauty;
            color: $red;
            border-block:1px solid $gray;
            display: flex;
            justify-content: center;
            align-items:center;
            height: 100px;
            h3{
                font-size: 3vw;
                margin: 0;
                font-family: 'Outfit',sans-serif;
                font-style: normal;
                font-weight: 900;
                // line-height: 121px;
                letter-spacing: 0.03em;
            }
           }
           .text{
            padding-inline-start: 50px;
            color:$gray;
            width: 100%;
            grid-area: p;
            display: flex;
            flex-direction: column;
            gap: 2rem;
           }
           .button{
            grid-area: button;
            height: fit-content;
            align-self: center;
            margin: 0 auto;
            a{
                text-decoration: none;
                text-align: center;
                color: $red;
                padding: 10px 20px;
                border: 3px solid $red;
                margin-bottom: 2rem;
                display: block;
                font-family: 'Outfit';
                font-style: normal;
                font-weight: 700;
                font-size: 24px;
                line-height: 120%;
                position: relative;
                overflow: hidden;
                background-color: transparent;
                transition: .3s;
            }
            a::before {
                content: '';
                width: 0;
                height: 400%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) rotate(45deg);
                background: $red;
                transition: .5s ease;
                display: block;
                z-index: -1;
               }
               a:hover::before {
                width: 105%;
               }
               a:hover {
                color: $white;
               }
           }
           .links{
            width: 100%;
            grid-area: link;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
            gap: 3rem;
            a{
                font-size: 1.5rem;
                text-align: center;
                text-decoration: none;
                color: $gray; 
                padding: 10px;
                // width: 180px;
                display: flex;
                justify-content: center;
                align-items:center;
                transition: color 0.5s ease-in;
                transition: border 0.5s ease-in;
                svg{
                    fill: $red;
                    transition: all 0.5s ease-in;
                }
                span{
                    
                    color: $gray;
                    transition: color 0.5s ease-in;
                }
               }
               a:hover{
                
                border: 1px solid $white;
                color: $white;
                .inst{
                    // fill: $gray;
                    transform: rotate(360deg);
                }
                .phone{
                    animation: shake 0.5s;
                    animation-iteration-count: infinite;
                }
                span{
                    color: $red; 
                }
             }
           }
    }
}


@keyframes shake {
    0% { transform: rotate(0deg); }
    25% { transform: rotate(5deg); }
    50% { transform: rotate(-5deg); }
    75% { transform: rotate(5deg); }
    100% { transform: rotate(0deg); }
  }