.contact{
    width: 100vw;
    height: 100%;
//   height: calc(var(--vh, 1vh) * 100);
    background-color:$white;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    top: 0;
    .content{
        height: 80vh;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding-inline: 5vw;
        width: calc(100vw - 5vw);
        h2{
            color:$red;
            font-size: clamp(1rem, 10vw, 3rem); 
        }
        .text{
            color:black;
        }
        .beuty-text{
            width: 100vw;
            border-block: 1px solid $gray;
            text-align: center;
            color:$red;
            font-size: 3rem;
            display: none;
           
            h3{
                padding-block: 1rem;
                margin: 0;
            }
        }

        .button{
            text-decoration: none;
            color:$red;
            border: 2px solid $red;
            padding-block: 0.8rem;
            width: 200px;
            text-align: center;
            font-size: 1.2rem;
        }
        .links{
            display: flex;
            flex-direction: column;
            gap: 20px;
            .link{
                display: flex;
                align-items:center;
                gap: 2rem;
                color:$black;
            }
        }
    }
    footer{
        background-color:#333333;
        height: 15vh;
        color:lighten($black, 30%);
        display: flex;
        flex-direction: column;
        align-items:center;
        padding-block-start: 1rem;
        p{
            span{
                color:$black;
            }
        }
        .designed{
            text-decoration: none;
            color: goldenrod;
        }
    }
}