.slide {
  // height: 100vh;
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
  scroll-snap-align: start;
  scroll-snap-stop: always;
  height: 100%;
  // height: calc(var(--vh, 1vh) * 100);
}

.slide.active {
  opacity: 1;
}

.bullets {
  position: fixed;
  right: 5vw;
  top: 50%;

  transform: translateY(-50%);
}

.bullet {
  width: 4px;
  height: 25px;
  border-radius: 6px;
  background-color: $red;
  transition: height 0.3 ease-in;
  margin: 15px 0;
  cursor: pointer;
  a {
    width: 4px;
    height: 25px;
    display: block;
  }
}

.bullet.isActive {
  background-color: lighten($color: $red, $amount: 10%);
  height: 35px;
  animation: bullets 1s ease-in-out infinite;
}

@keyframes bullets{
  0%{
    background-color: lighten($color: $red, $amount: 10%);
    // width: 4px;
  }
  50%{
    background-color: $red;
    // width: 6px;
  }
  100%{
    // width: 4px;
    background-color: lighten($color: $red, $amount: 10%);
  }
}