.educathion {
  width: 100vw;
  height: 100%;
  // height: calc(var(--vh, 1vh) * 100);
  background-color: $white;
  display: flex;
  flex-direction: column;
  justify-content:space-evenly;;
  gap: 5vh;
  padding-top: 5vh;
  padding-inline: 5vw 15vw;
  position: relative;
  overflow-y: hidden;
  
  h2 {
    margin: 0;
    color: $red;
    word-wrap: break-word;
    font-size: clamp(1rem, 10vw, 3rem); 
    line-height: 50px;
    letter-spacing: 0.01em;
  }
  .text {
    color: $black;
    // line-height: 130%;
    padding-inline: 0 10vw;
    p:first-of-type{
      padding-bottom: 1rem;
    }
  }
  .educathion-button{
    border: 3px solid #ED3E2E;
    // text-align: center;
    width: 228px;
    a{
      font-family: 'Outfit', sans-serif;
      font-weight: $bigFontWeight;
      font-size: 1.5remn;
      display: block;
      text-decoration: none;
      color:$red;
      padding: 1rem
    }
  }
  }


.motiv{
    @extend .educathion;
   
}