.main-head{
    flex-shrink: 0;
    width: 100vw;
    height: 100vh;
    background-color:$white;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    row-gap: 8vh;
    // flex-direction: row-reverse;
    // gap: 3rem;
    // justify-content: space-evenly;
    // align-items:center;
    padding-inline:5vw;
    // overflow-x: hidden;
    // position: sticky;
    // top: 0;
    overflow-x: visible;
    .hero-nav{
        display: flex;
        cursor: pointer;
        li{
          position: relative;
          height: fit-content;
          font-family: 'Outfit',sans-serif;
          font-style: normal;
          font-weight: 600;
          font-size: 20px;
          line-height: 25px;
          color: $gray;
        }
        li::after {
          content: "";
          position: absolute;
          width: 0;
          height: 2px;
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
          background-image: linear-gradient($red 50%, $red 50%);
          transition: width 0.3s ease-in-out;
        }
        li:hover::after {
          width: 100%;
        }
    }
    .head-text{
        grid-area: 2 / 2 / 3 / 3;
        h1{
          font-family: 'Outfit',sans-serif;
            margin: 0;
            color:$red;
            word-wrap: break-word;
            font-size: 5rem;
            font-style: normal;
            font-weight: 900;
            line-height: 111px;
            letter-spacing: 0.01em;

        }
        
        p{
            color:$black;
            // padding-block:5vh;
            max-width: 500px;
            font-size: 1.5rem;
        };
    }

    .head-logo{
        grid-area: 2 / 1 / 3 / 2;
        align-self: center;
        justify-self: center;
        width: 20vw;
        height: auto;
        .hero-logo{
          width: 20vw;
          height: auto;
          opacity: 0.4;
          stroke: black;
          fill: transparent;
          stroke-dasharray: 4200;
          stroke-dashoffset: 4200;
          animation: anim 20s forwards linear;
          transition: all 5 ease-in;
          path {
            
            transition: transform 0.3s ease, box-shadow 0.3s ease;
          }
          .to-change{
            stroke: #66666A;
            transition: transform 0.3s ease, box-shadow 0.3s ease;
          }
          .contact-changes{
            stroke: #66666A;
          }
          .shadow {
            box-shadow: 5px 5px 50px rgba(164, 9, 9, 0.7);
          }
          
          /*    */
          .left {
            transform-origin: left;
          }
          .right {
            transform-origin: right;
          }
          .top {
            transform-origin: top;
          }
          .bottom {
            transform-origin: bottom;
          } 
        
        }
        
       
     
      @keyframes anim{
       
          to{
            opacity: 1;
              stroke-dashoffset: 0;
              stroke: 0.5 rgb(55, 52, 52);
          }
      }
    }
    
  
    
}



.run-line {
    grid-area: 3 / 1 / 4 / 3;
    position: absolute;
    // flex: 1 1 auto;
    // overflow: hidden;
    display: flex;
    gap: 1rem;
    width: 100%;
    align-items:center;
  }
  
  .run-line .js-marquee-wrapper {
    position: absolute;
    display: flex;
    gap: 2rem;
    white-space: nowrap;
    color:$lightgreen
  }
  
  
  
 