.esthethic{
    display: none;
    width: 100vw;
    height: 100vh;
    background-color:#333333;
    // display: flex;
    gap: 3rem;
    padding-inline: 5vw 15vw;
    // position: sticky;
    // top: 0;
    // flex-wrap: nowrap;
    overflow-x: scroll;
    scroll-snap-type: y mandatory;
    position: relative;
    h2{
        margin: 0;
        color:$red;
        word-wrap: break-word;
        font-size: 40px;
        line-height: 50px;
        letter-spacing: 0.01em;
        
    }
    .text{
        color:$white;
        line-height: 130%;
        padding-inline: 0 10vw;
    }
    
}
.esthethic.active{
    .arrows{
        display: none;
        width: 124px ;
        position: fixed;
        display: flex;
        gap: 1.5rem;
        left: calc(50% - 62px);
        top: 90vh;
        display: none;
        .arrow{
            fill:#DADADA;
        }
        .arrow:disabled{
            fill: #949191;
        } 
    }
}

.esthethic-section{
    display: none;
    flex: 0 0 auto;
    width: 100vw;
    height: 100vh;
    background-color:#333333;
    
    flex-direction: column;
    gap: 1rem;
    justify-content: end;
    padding-top: 5rem;
    padding-bottom: 150px;
    padding-inline: 5vw 15vw;
    position: relative;
    scroll-snap-align: start;
    overflow-x: hidden;
    h2{
        margin: 0;
        color:$red;
        word-wrap: break-word;
        font-size: 40px;
        line-height: 50px;
        letter-spacing: 0.01em;
        
    }
    .text{
        color:$white;
        line-height: 130%;
        padding-inline: 0 10vw;
    }
    .logo{
        position: absolute;
        left: -20%;
        top: 30px;
        opacity: 0;
        transition: opacity 2s ease;
    }
}


.active-slide{
    .logo{
        opacity: 1;
        transition: 1s ease;
    }
}


.section2{
    .logo{
        svg{
            .forehead{
                stroke: $red;
            }
        }
    }
}
.section3{
    .logo{
        svg{
            .eyes{
                stroke: $red;
            }
        }
    }
}