.menu-third{
    width: 100%;
    height: 100%;
    // background-color: lightgray;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-inline: 50px;
    gap: 30px;
   h2{
    color:$red;
   } 
   p{
    max-width: 80%;
    color: $white;
   }
   a{
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    color: $red;
    padding: 10px;
    border: 1px solid $red;
    // width: 180px;
    transition: all 0.5s ease-in-out;
    white-space: nowrap;
    align-self:baseline;
   }
   a:hover{
    color: $white;
    border: 1px solid $white;
   }
}