.slide {
    // height: 100vh;
    opacity: 0.5;
    transition: opacity 0.5s ease-in-out;
    scroll-snap-align: start;
  height: 100vh;
  }
  
  .slide.active {
    opacity: 1;

  }
  
  .bullets {
    position: fixed;
    right: 20px;
    top: 60%;
    
    transform: translateY(-50%);
    
  }
  
  .bullet {
    width: 7px;
    height: 30px;
    border-radius: 3px;
    background-color: $red;
    margin: 25px 0;
    cursor: pointer;
    a{
      width: 4px;
      height: 30px;
      display: block
    };
  }
  
  .bullet.isActive {
    background-color: $red;
    height: 60px;
  }
  