.menu-second {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: relative;
  // background-color: #555;

  .slide-estet {
    position: absolute;
    top: 50%;
    left: calc(50% - 30%);
    transform: translateY(-50%);
    width: 60%;
    display: flex;
    flex-direction: column;
    gap: 30px;
    opacity: 0;
    transition: opacity 1s ease-in;
  }
  .active-estet {
    opacity: 1;
    transition: opacity 1s ease-in;
  }
  .estet-nav {
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    list-style: none;
    color: gray;
    border-top: 1px solid $gray;
    padding: 0;
    animation: border 3s ease-in infinite;
 
    li {
      // width: 0;
      // height: 0;
      // border-top: 100px solid red;
      // border-right: 25px solid transparent;
      // border-bottom: 100px solid red;
    }
    a {
      display: inline-block;
      padding: 5px 10px;
      color: $gray;
      border: 1px solid $gray;
      // border-radius:0 0 50px 0;
      // transform: skew(-20deg);
      text-decoration: none;
      background-color: #333333;
      position: relative;
    }
    a::after {
      content: "";
      position: absolute;
      width: 0;
      height: 2px;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      background-image: linear-gradient(to right, $red 50%, $red 50%);
      transition: width 0.3s ease-in-out;
    }
    a:hover::after {
      width: 100%;
    }
    a:hover{
      color:$white;
    }
    .active a {
      color: $white;
    }
    .first {
      transform: translateY(-103%);
      margin-inline-end: -20%;
      a{
        border:none;
        font-size: small;
      }
    }
  }
  .estet-text {
    .estet-title {
      color: $red;
      font-size: 1.5rem;
    }
    p {
      color: $white;
      margin: 0;
    }
  }
  .forehead-text {
    h3 {
      color: $red;
      font-size: 1.5rem;
    }
    p {
      color: $white;
    }
  }
  .glass-text {
    h3 {
      color: $red;
      font-size: 1.5rem;
    }
    p {
      color: $white;
    }
  }
  .nouse-text {
    h3 {
      color: $red;
      font-size: 1.5rem;
    }
    p {
      color: $white;
    }
  }
  .skull-text {
    h3 {
      color: $red;
      font-size: 1.5rem;
    }
    p {
      color: $white;
    }
  }
  .lips-text {
    h3 {
      color: $red;
      font-size: 1.5rem;
    }
    p {
      color: $white;
    }
  }
  .podbor-text {
    h3 {
      color: $red;
      font-size: 1.5rem;
    }
    p {
      color: $white;
    }
  }
}


@keyframes border {
  0% {
    border-top: 1px solid $gray;
  }
  50% {
    border-top: 1px solid lighten($gray,20%);
  }
  100%{
    border-top: 1px solid $gray;
  }
}




