.menu{
    flex-shrink: 0;
    height: 100vh;
    width: 60vw;
    background-color: #333333;
    position: relative;
    display: flex;
    scroll-snap-type: x mandatory;
    // scroll-snap-stop: always;
    // flex-direction: column;
    .menu-nav{
        list-style: none;
        display: inline-flex;
        justify-content: space-around;
        position: absolute;
        top: 3vh;
        left: 0;
        width: 100%;
        padding: 0;
        z-index: 3;
        .nav-link{
            cursor: pointer;
            color: $white;
            font-size: 20px;
            font-weight: 600;
            position: relative;
        }
        .nav-link::after {
            content: "";
            position: absolute;
            width: 0;
            height: 2px;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            background-image: linear-gradient(to right, $red 50%, $red 50%);
            transition: width 0.3s ease-in-out;
          }
        .nav-link:hover::after {
            width: 100%;
          }
    }
    .menu-item{
        flex-shrink: 0;
        background-color: #333333;
        scroll-snap-stop: always;
    }
}

.menu-first{
    align-self: center;
    width: 100%;
    text-align: end;
    padding-inline: 3rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
   
    
    h2{
        color:$red;
        padding-inline-end: 50px;
        padding-block-end: 3rem;
    }
    .exp-nav-wrapper{
        display: flex;
        justify-content: end;
        // padding-inline: 3rem;
        .exp-nav{
            color: $white;
            display: flex;
            flex-direction: column;
            align-items:flex-end;
            gap: 1.8rem;
            padding-inline-end: 50px;
            position: relative;
            a{
                cursor: pointer;
                position: relative;
            }
            a::after {
                content: "";
                position: absolute;
                width: 0;
                height: 2px;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                background-image: linear-gradient(to right, $red 50%, $red 50%);
                transition: width 0.3s ease-in-out;
              }
            a:hover::after{
                width: 100%;
            }
        }
        .red-line{
            width: 5px;
            height: 100%;
            background-color: $red;;
        }
    }
    
}

