html{
  overflow: hidden;
font-family: Outfit;
max-height: 100%;
max-height: 100%;
// overflow: hidden;
}
body {
  @include getCssVar(font-family);
  @include getCssVar(background-color, $white);
  font-size: 1rem;
  position: relative;
  height: 100vh;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
main{
  
  // height: 100svh;
  // min-height: 100%;
  flex: 1;
  // display: flex;
  // flex-direction: column;
  // height: calc(var(--vh, 1vh) * 100);
  overflow-y: scroll;
  overflow-x: scroll;
  scroll-snap-type: y mandatory;
}


.menu{
  display: none;
}

*::-webkit-scrollbar {
  display: none
}